@import "../../inc/vars";
.acro-tabs-block {
	padding: 50px 20px;
	width: 1400px;
	max-width: 100%;
	margin: 0 auto;
	@media (min-width: $screen-sm){
		padding: 110px 20px;
	}
	&__title {
		text-align: center;
		h2 {
			@include titleGrad();
			@include pop(400, 30, 1.24);
			letter-spacing: -0.6px;
			margin-bottom: 31px;
			@media (min-width: $screen-sm){
				font-size: 36px;
				margin-bottom: 28px;
				letter-spacing: -0.72px;
			}
		}
		&__description {
			color: #4F4B58;
			@include fig(400, 20, 1.56);
			margin-bottom: 67px;
			@media (min-width: $screen-sm){
				font-size: 24px;
				margin-bottom: 51px;
				width: 80%;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
	.acro-tabs__content {
		&__tabs {
			display: flex;
			align-items: center;
			overflow-x: auto;
			margin-bottom: 38px;
			-ms-overflow-style: none;
			scrollbar-width: none;
			&::-webkit-scrollbar {
				display: none;
			}
			@media (min-width: $screen-md){
				overflow-x: visible;
				flex-wrap: wrap;
			}
			@media (min-width: $screen-sm){
				justify-content: center;
				margin-bottom: 74px;
			}
			&__item {
				height: 56px;
				color: $purple;
				@include pop(400, 13, 1.36);
				padding: 5px 15px;
				text-align: center;
				background: #fff;
				display: flex;
				align-items: center;
				min-width: 180px;
				transition: .3s;
				cursor: pointer;
				position: relative;
				justify-content: center;
				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					background: #fff;
					transition: .3s;
					box-shadow: 1px 2px 2.269px 0 rgba(7, 4, 146, .1);
				}
				&:after {
					content: '';
					display: block;
					position: absolute;
					left: 100%;
					top: 4px;
					bottom: 4px;
					height: auto;
					width: 1px;
					background: #BABFC9;
					z-index: 2;
					@media (min-width: $screen-sm){
						top: 7px;
						bottom: 7px;
					}
				}
				span {
					position: relative;
					z-index: 1;
				}
				@media (min-width: $screen-sm){
					height: 67px;
					font-size: 16px;
					min-width: 150px;
				}
				&:first-child {
					border-radius: 10px 0px 0px 10px;
					&:before {
						border-radius: 10px 0px 0px 10px;
					}
				}
				&:last-child {
					border-radius: 0 10px 10px 0;
					&:before {
						border-radius: 0 10px 10px 0;
					}
					&:after {
						display: none;
					}
				}
				&.active {
					color: #fff;
					z-index: 1;
					&:before {
						box-shadow: 2px 2px 4px 0px rgba(7, 4, 146, 0.10), 24px 24px 60px 0px rgba(6, 47, 125, 0.05), 12px 12px 24px 0px rgba(27, 59, 119, 0.05), 4px 4px 20px 0px rgba(83, 45, 159, 0.25);	
						background: $purple;
						border-radius: 10px;
					}
				}
			}
		}
		&__items {
			.list-item {
				display: none;
				&.active {
					display: flex;
					align-items: center;
					flex-wrap: wrap;
				}
				&__image {
					width: 100%;
					order: 2;
					@media (min-width: $screen-sm){
						order: -1;
						width: 40%;
						display: flex;
						justify-content: center
					}
					&-wrapper {
						position: relative;
						text-align: center;
						img {
							position: relative;
							z-index: 1;
							width: 350px;
							max-width: 100%;
						}
						&:before {
							content: '';
							display: block;
							background: url('../../../../images/svg/tabs-bg.svg') no-repeat center center;
							position: absolute;
							width: 120%;
							height: 100%;
							left: -20%;
							top: 0;
							background-size: contain;
						}
					}
				}
				&__link {
					padding-top: 30px;
					a {
						color: $purple;
						@include pop(500, 17, 1.24);
						text-transform: uppercase;
						letter-spacing: 0.349px;
						@media (min-width: $screen-sm){
							font-size: 18px;
							letter-spacing: 0.339px;
						}
					}
				}
				.listContent {
					width: 100%;
					padding: 20px;
					text-align: center;
					margin-bottom: 45px;
					@media (min-width: $screen-sm){
						margin-bottom: 0;
						width: 60%;
						padding: 30px;
						text-align: left;
					}
					h3 {
						color: $purple;
						@include pop(400, 30, 1.36);
						margin-bottom: 23px;
						letter-spacing: -0.6px;
						@media (min-width: $screen-sm){
							font-size: 36px;
							letter-spacing: -0.72px;
						}
					}
					.list-item__description {
						color: #4F4B58;
						@include fig(400, 20, 1.56);
						@media (min-width: $screen-sm){
							font-size: 24px;
						}
					}
				}
			}
		}
	}
	&_2 {
		.acro-tabs-block__title {
			margin-bottom: 42px;
			@media (min-width: $screen-sm){
				margin-bottom: 94px;
			}
		}
		.acro-tabs__content {
			&__tabs {
				@media (min-width: $screen-sm){
					margin-bottom: 20px;
				}
				&__item {
					@media (min-width: $screen-sm){
						box-shadow: 1px 2px 7.269px 0 rgba(7, 4, 146, .1);
						&.first {
							&:before {
								border-radius: 0 0 0 10px;
							}
							border-radius: 0 0 0 10px;
							&.row-1 {
								&:before {
									border-radius: 10px 0 0 10px;
								}
								border-radius: 10px 0 0 10px;
							}
						}
						&.last {
							&:before {
								border-radius: 0 0 10px 0;
							}
							&:after {
								display: none;
							}
							border-radius: 0 0 10px 0;
							&.row-1 {
								&:before {
									border-radius: 0 10px 10px 0;
								}
								border-radius: 0 10px 10px 0;
							}
						}
					}
				}
			}
			&__items {
				@media (min-width: $screen-sm){
					padding: 0 50px;
				}
				.list-item {
					.listContent {
						@media (min-width: $screen-sm){
							width: 50%;
						}
					}
					&__image {
						order: 2;
						@media (min-width: $screen-sm){
							width: 50%;
							aspect-ratio: 1.1;
							display: flex;
							align-items: center;
						}
						&-wrapper {
							img {
								width: 100%;
							}
							&:before {
								display: none;
							}
						}
					}
				}
			}
		}
	}
}